   .f-our-vision-container {

    display: flex
;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    margin-left: 88px;
    padding: 30px;
    gap: 48px;
}

.f-our-vision-image {
flex: 1;
flex-direction: row;
gap: 0px;
width:586px;
height: 464px;
}

.visionbg{
width:176px;
height: 176px;
margin-bottom: 147px;
margin-left: 62px;
}
.vision1 {
width: 178px;
height: 384px;
margin-bottom: -165px;
margin-left: -144px;
transition: transform 0.3s ease, box-shadow 0.3s ease;
border-radius: 20px;
}

.vision2 {
width: 178px;
height: 384px;
margin-bottom: -82px;
margin-left: 6px;
transition: transform 0.3s ease, box-shadow 0.3s ease;
border-radius: 20px;
}

.vision3 {
width: 178px;
height: 384px;
margin-top: -274px;
margin-left: 462px;
transition: transform 0.3s ease, box-shadow 0.3s ease;
border-radius: 20px;
}

/* Hover Effects */
.vision1:hover, .vision2:hover, .vision3:hover {
transform: scale(1.05);
box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2); 
cursor: pointer; 
}


.f-our-vision-image {
width: 558px; 
height: 559px; 
border-radius: 15px;
margin-left:40px
}


.f-our-vision-content {
flex: 1;
margin-left: 20px;
padding: 55px;
width:50%
}

.f-vision-title {
font-size: 50px;
font-weight: 400; 
color: #ff6600;
margin: 0 0 10px 0; 
font-family: "Lilita One", sans-serif;
}

.f-vision-description {
font-size: 24px;
font-weight: 400; 
color: #555;
line-height: 1.4; 
margin-bottom: 10px; 
}

.f-vision-subtext {
font-size: 22px;
color: #666;
line-height: 1.3; 
margin-top: 10px; 
}
/* Responsive adjustments for 1024px */
@media (max-width: 1024px) {
  .f-our-vision-container {
      flex-direction: row;
      gap: 0px;
      padding: 20px;
      margin-left: 0;
  }

  .f-our-vision-image {
      width: 450px;
      height: 450px;
  }

  .visionbg {
      width: 140px;
      height: 140px;
      margin-bottom: 146px;
      margin-left: -12px;
  }

  .vision1, .vision2, .vision3 {
      width: 150px;
      height: 320px;
      
  }
  .vision1{
    margin-bottom: -117px;
    margin-left: -115px;
  }
  .vision2{
    margin-bottom: -56px;
  }
  .vision3{
    margin-bottom: -61px;
    margin-left: 329px;
}

  .f-our-vision-content {
      padding: 40px;
      width: 100%;
      padding:26px;
      margin-left:45px;
      text-align: center;
  }

  .f-vision-title {
      font-size: 50px; /* Reduced font size */
  }

  .f-vision-description {
      font-size: 27px; /* Reduced font size */
  }

  .f-vision-subtext {
      font-size: 23px; /* Reduced font size */
  }
}

